<script lang="ts">import Message from './Message.svelte';
import Decision from './Decision.svelte';
import { derived, get, writable } from 'svelte/store';
;
import { tweened } from 'svelte/motion';
import { createEventDispatcher } from 'svelte';
import { tableState } from './stores/table';
import { clearMessage } from './stores/game';
import { paused } from './stores/user';
import { setPauseSafeTimeout } from './stores/timeout';
const IS_VILLAIN_DEBUG = false;
const dispatch = createEventDispatcher();
export let profile;
export let index;
const indexStore = writable(index);
const player = derived([tableState, indexStore], ([$state, $index]) => {
    return $state.players[index];
});
const message = derived(player, $player => $player.message);
let style = '';
let messageStyle = '';
let profileStyle = '';
let frameStyle = '';
let talkingSource = tweened(0, {
    duration: (_, val) => val === 0 ? 0 : 100000,
});
let bounce = derived(talkingSource, $talkingSource => {
    const CYCLE_LENGTH = ($player === null || $player === void 0 ? void 0 : $player.image) === 'drink' ? 1500 : 850;
    const SWAY_AMOUNT = ($player === null || $player === void 0 ? void 0 : $player.image) === 'drink' ? 30 : 15;
    const progress = ($talkingSource % CYCLE_LENGTH) / CYCLE_LENGTH;
    if (progress < 0.25) {
        return (progress * 2) * SWAY_AMOUNT;
    }
    else if (progress < 0.75) {
        return (progress - 0.25) * 2 * -SWAY_AMOUNT + SWAY_AMOUNT / 2;
    }
    else {
        return (progress - 0.75) * 2 * SWAY_AMOUNT - SWAY_AMOUNT / 2;
    }
});
function onPlaying(event) {
    if ($paused) {
        return;
    }
    const isPlaying = event.detail.isPlaying;
    setPauseSafeTimeout(() => {
        if ($paused) {
            return;
        }
        if (!($message === null || $message === void 0 ? void 0 : $message.light)) {
            talkingSource.set(isPlaying ? 100000 : 0);
        }
        if (!isPlaying) {
            clearMessage(index);
        }
        dispatch('playing', { isPlaying });
    }, isPlaying ? 0 : ($message === null || $message === void 0 ? void 0 : $message.postDelay) === undefined ? 2500 : $message.postDelay);
}
$: {
    indexStore.set(index);
    style = `left: ${profile.x}px;top: ${profile.y}px;opacity: ${$message ? 0.9 : 0.6};transform: rotate(${$bounce}deg)`;
    messageStyle = `left: ${profile.x + profile.messageDx}px;top: ${profile.y + profile.messageDy}px`;
    profileStyle = `background-image: url('build/assets/portraits/${$player === null || $player === void 0 ? void 0 : $player.image}.png')`;
    frameStyle = `filter: brightness(${$message ? $message.light ? 50 : 100 : 0}%)`;
    if (!$message) {
        talkingSource.set(0);
    }
}
;
</script>

<div class="profile" style={style}>
    {#if $player?.image}
        <div class="profile-image" style={profileStyle}></div>
    {/if}
    <div class="profile-frame" style={frameStyle}></div>
    <div class="profile-overlay">
        <Decision decision={$player?.decision}></Decision>
        {#if IS_VILLAIN_DEBUG}
            <p class="cheat">{$player.villainRound}</p>
        {/if}
    </div>
</div>
<div class="message-container" style={messageStyle}>
    <Message message={$message} voice={$player?.image} on:playing={onPlaying}></Message>
</div>

