<script lang="ts">import Card from './Card.svelte';
import Chip from './Chip.svelte';
import Territory from './Territory.svelte';
import Timeline from './Timeline.svelte';
import Profile from './Profile.svelte';
import Decide from './Decide.svelte';
import Counter from './Counter.svelte';
import Clues from './Clues.svelte';
import TableText from './TableText.svelte';
import positionStore from './stores/world-position';
import { bet, choose, getChoices, step, setTimeToCurrent, setTime, setMessage } from './stores/game';
import { get } from 'svelte/store';
import { sumCards, sumTableCards } from './stores/deck';
import { tableState } from './stores/table';
import { getPlayerCards, getAllTerritories, getPlayerBankChips, PROFILE_DATA, getPlayerBetChips, COUNTER_POSITIONS, getDealerCards, DEALER_COUNTER_POSITION, TEXT_NUMBER_POSITIONS } from './stores/position';
;
import { deciding, choices, paused, bets, ended } from './stores/user';
import { onGameStep } from './stores/history';
import { registerSound, playSound, initMusic, playMusic, pauseMusic, setMusicSpeed } from './stores/audio';
let opacity = 0;
const territories = getAllTerritories();
$: counts = [0, 1, 2, 3, 4].map(index => {
    return sumTableCards(getPlayerCards($tableState, index));
});
$: dealerCount = sumTableCards(getDealerCards($tableState), true);
$: backgroundStyle = $paused ? 'filter: grayscale(100%) brightness(0.8)' : '';
function playerChoose(event) {
    if (event.detail.value === 'STAND') {
        playSound('interact');
    }
    deciding.set(false);
    choose(event.detail.value, 2);
    choices.set([]);
    runWithDelay(500);
}
function playerBet(event) {
    if (event.detail.value === 0) {
        playSound('interact');
    }
    deciding.set(false);
    bet(event.detail.value, 2);
    bets.set([]);
    runWithDelay(500);
}
function runWithDelay(delay) {
    setTimeout(() => run(), delay);
}
function run() {
    if ($paused) {
        return;
    }
    const stepResult = step();
    // Hacky bit to stop these sounds from playing on init.
    if (stepResult.lastAction.kind === 'RESET_NODE') {
        registerSound('card', {
            path: 'cardSlide3.wav',
            volume: .2,
            maxInstances: 5,
        });
        registerSound('chip', {
            path: 'chipsStack3.wav',
            volume: .2,
            maxInstances: 10,
        });
        registerSound('chip-low', {
            path: 'chipsStack4.wav',
            volume: .2,
            maxInstances: 10,
        });
        registerSound('chip-high', {
            path: 'chipsHandle4.wav',
            volume: .2,
            maxInstances: 10,
        });
        registerSound('interact', {
            path: 'chipLay3.wav',
            volume: .2,
            maxInstances: 1,
        });
    }
    let handledByPlayer = false;
    if (stepResult.lastAction.kind === 'BET_PLAYER' && stepResult.lastAction.target === 2) {
        const newBets = [];
        for (let i = 1; i <= Math.min(getPlayerBankChips(stepResult.endState, 2).length, 5); i++) {
            newBets.push(i);
        }
        bets.set(newBets);
        deciding.set(true);
        handledByPlayer = true;
    }
    if (stepResult.lastAction.kind === 'DECIDE_PLAYER' && stepResult.lastAction.target === 2) {
        const options = getChoices(stepResult.lastAction.target);
        if (options.length > 0) {
            choices.set(options);
            deciding.set(true);
            handledByPlayer = true;
        }
    }
    if (stepResult.lastAction.kind === 'RESET_NODE' && getPlayerBankChips(stepResult.endState, 2).length <= 0) {
        ended.set('LOSE_MONEY');
    }
    onGameStep(stepResult, () => {
        run();
    }, handledByPlayer);
}
setTimeout(() => {
    run();
}, 2000);
registerSound('pause', {
    path: 'pause.ogg',
    volume: .5,
    maxInstances: 1,
});
registerSound('play', {
    path: 'play.ogg',
    volume: .5,
    maxInstances: 1,
});
initMusic(0, 'build/assets/mus/synth.ogg', 0.05);
initMusic(1, 'build/assets/mus/ambient.mp3', 0.02);
playMusic(0);
playMusic(1);
function onPause() {
    playSound('pause');
    setMusicSpeed(0, 0.5, 0.025);
    setMusicSpeed(1, 0.5, 0.015);
    paused.set(true);
}
function onResume() {
    playSound('play');
    paused.set(false);
    setTimeToCurrent();
    setTimeout(() => {
        setMusicSpeed(0, 1, 0.05);
        setMusicSpeed(1, 1, 0.02);
    }, 500);
}
function setTimeline(event) {
    setTime(event.detail.value);
}
function addMessages() {
    const toSend = [
        {
            target: 0,
            message: {
                text: 'Don\'t you worry about Ol\' Monty. My day\'s a comin.',
                speed: 1,
            },
        },
        {
            target: 1,
            message: {
                text: 'Just keep your head on your shoulders and you\'ll be fine.',
                speed: 1,
            },
        },
        {
            target: 3,
            message: {
                text: 'Sorry, I don\'t sign autographs at the table.',
                speed: 1.5,
            },
        },
        {
            target: 4,
            message: {
                text: 'yah know i looked just like you when i was younger',
                speed: 1,
            },
        },
    ];
    for (let i = 0; i < toSend.length; i++) {
        setTimeout(() => {
            setMessage(toSend[i].message, toSend[i].target);
        }, 1000 * i);
    }
}
function win() {
    ended.set('WIN');
}
function lose() {
    ended.set('LOSE');
}
setTimeout(() => {
    opacity = 1;
}, 500);
</script>

<div class="game-container" style="opacity: {opacity}">
    <div class="table-background" style={backgroundStyle}></div>
    {#each $tableState.cards as card}
        <Card card={card}></Card>
    {/each}
    {#each $tableState.chips as chip}
        <Chip chip={chip}></Chip>
    {/each}
    <!-- {#each territories as territory}
        <Territory territory={territory}></Territory>
    {/each} -->
    {#each PROFILE_DATA as profile, index}
        <Profile profile={profile} index={index}></Profile>
    {/each}
    {#if !$paused && $deciding}
        <Decide on:choose={playerChoose} on:bet={playerBet}></Decide>
    {/if}
    {#each counts as count, index}
        <Counter count={count} position={COUNTER_POSITIONS[index]}></Counter>
    {/each}
    {#each TEXT_NUMBER_POSITIONS as textPosition, index}
        <TableText count={index + 1} position={textPosition}></TableText>
    {/each}
    <Counter count={dealerCount} position={DEALER_COUNTER_POSITION}></Counter>
    <Timeline on:pause={onPause} on:resume={onResume} on:change={setTimeline}></Timeline>
    <Clues></Clues>
    <!-- <button on:click={addMessages}>Add Messages</button>
    <button on:click={win}>WIN</button>
    <button on:click={lose}>LOSE</button> -->
</div>

<style>
.game-container {
       position: absolute;
       left: 0;
       width: 1920px;
       height: 1080px;
       transition: opacity 1s ease;
   }

   .table-background {
       position: absolute;
       width: 100%;
       height: 100%;
       background-image: url('assets/background.png');
       transition: filter 2s ease;
       z-index: -1;
   }

   /* button {
       z-index: 1;
   } */
</style>