<script lang="ts">;
import { afterUpdate } from 'svelte';
import { playSound } from './stores/audio';
const PATH_PREFIX = 'build/assets/cards/';
let style = '';
export let card;
function getCardPath(card) {
    return `${card.value.toLocaleLowerCase()}_of_${card.suit.toLocaleLowerCase()}s.png`;
}
function getCardStyle(position, isHidden) {
    const path = `${PATH_PREFIX}${getCardPath(card.info)}`;
    let builtStyle = `background-image: url(${path});top: ${position.y}px;left: ${position.x}px;z-index:${position.z || 0}`;
    if (position.rotation !== 0) {
        builtStyle += `;transform: rotate(${position.rotation}deg)`;
    }
    if (isHidden) {
        builtStyle += ';filter: contrast(0%)';
    }
    return builtStyle;
}
let lastPosition = {
    x: -100,
    y: -100,
    rotation: 0,
};
$: {
    style = getCardStyle(card.position, card.isHidden);
    if (card.position.x !== lastPosition.x || card.position.y !== lastPosition.y) {
        playSound('card');
    }
    lastPosition = card.position;
}
</script>

<div class="card" style="{style}">
</div>

<style>
.card {
       position: absolute;
       width: 100px;
       height: 145px;
       background-size: cover;
       transform-origin: top left;
       transition: all .25s;
   }
</style>