<script lang="ts">;
import { tweened } from "svelte/motion";
const DECISION_MAP = {
    'HIT': 'build/assets/hit.png',
    'STAND': 'build/assets/stand.png',
    'DOUBLE': 'build/assets/double.png',
    'WIN': 'build/assets/win.png',
    'LOSE': 'build/assets/lose.png',
    'BUST': 'build/assets/bust.png',
    'PUSH': 'build/assets/push.png',
};
export let decision;
let decisionPath;
const decisionOpacity = tweened(0, {
    duration: 200,
});
$: {
    const newDecisionPath = DECISION_MAP[decision];
    // console.log('UPDATING DECISION', newDecisionPath);
    if (!newDecisionPath) {
        decisionOpacity.set(0);
    }
    else {
        decisionPath = newDecisionPath;
        decisionOpacity.set(1);
    }
}
</script>

{#if decisionPath}
    <div class="decision" style="background-image: url('{decisionPath}');opacity: {$decisionOpacity}"></div>
{/if}

<style>
.decision {
    position: absolute;
    z-index: 99999999;
    width: 180px;
    height: 180px;
    background-position: center;
    background-repeat: no-repeat;
}
</style>