<script lang="ts">import { clues, isCluesHidden } from './stores/clues';
import { paused } from './stores/user';
function onClick() {
    if (!$paused) {
        isCluesHidden.update(val => !val);
    }
}
</script>

<div class="clues" class:hidden={$isCluesHidden && !$paused} on:click={onClick}>
    <div class="clues-background"></div>
    <div class="clues-container">
        {#each $clues as clue}
            <div class="line">{clue}</div>
        {/each}
    </div>
    {#if !$paused}
        <img class="corner" class:corner-rotate={!$isCluesHidden} src="build/assets/corner.png" alt="corner">
    {/if}
</div>

