<script lang="ts">;
import { afterUpdate } from 'svelte';
let style = '';
export let territory;
function getTerritoryStyle(territory) {
    const top = territory.y - territory.height / 2;
    const left = territory.x - territory.width / 2;
    return `top: ${top}px;left: ${left}px;width: ${territory.width}px;height: ${territory.height}px;transform: rotate(${territory.rotation}deg)`;
}
$: {
    style = getTerritoryStyle(territory);
}
;
</script>

<div class="territory" style="{style}">
</div>

<style>
.territory {
       position: absolute;
       border: 2px solid red;
       z-index: 100000000000;
   }
</style>
