<script lang="ts">import { timelineInfo } from './stores/game';
import { createEventDispatcher } from 'svelte';
import { paused } from './stores/user';
const dispatch = createEventDispatcher();
let rangeStart;
let rangeEnd;
let jumpPrevious;
let jumpNext;
let currentHand;
let numHands;
$: {
    rangeStart = 0;
    rangeEnd = $timelineInfo.length - 1;
    const keyframes = $timelineInfo.resetFrames;
    for (let i = 0; i < keyframes.length; i++) {
        const keyframe = keyframes[i];
        if (keyframe <= $timelineInfo.currentIndex) {
            rangeStart = keyframe;
            jumpPrevious = Math.max(rangeStart - 1, 1);
            currentHand = i + 1;
        }
    }
    for (let i = keyframes.length - 1; i >= 0; i--) {
        const keyframe = keyframes[i];
        if (keyframe > $timelineInfo.currentIndex) {
            rangeEnd = keyframe - 1;
            jumpNext = keyframes[i + 1] ? keyframes[i + 1] - 1 : $timelineInfo.length - 1;
        }
    }
    numHands = keyframes.length;
}
function handleChange({ target }) {
    dispatch('change', {
        value: target.value,
    });
}
let pauseEnabled = true;
function disablePause(length) {
    pauseEnabled = false;
    setTimeout(() => {
        pauseEnabled = true;
    }, length);
}
disablePause(3000);
function handlePause() {
    if (pauseEnabled) {
        dispatch('pause');
        disablePause(1000);
    }
}
function handleResume() {
    if (pauseEnabled) {
        dispatch('resume');
        disablePause(1000);
    }
}
function handleBack() {
    dispatch('change', {
        value: jumpPrevious,
    });
}
function handleForward() {
    dispatch('change', {
        value: jumpNext,
    });
}
</script>

<div class="timeline">
    <div class="control-container" class:hidden-control={!$paused}>
        <div class="hand"><span>Hand: {currentHand} / {numHands}</span></div>
        <img class="play" on:click={handleResume} alt="resume" src="build/assets/play.png">
        <img class="back" on:click={handleBack} alt="back" src="build/assets/back.png">
        <img class="forward" on:click={handleForward} alt="forward" src="build/assets/forward.png">
        <input type="range" min={rangeStart} max={rangeEnd} step="1" value={$timelineInfo.currentIndex} on:input={handleChange}>
    </div>
    <div class="control-container" class:hidden-control={$paused}>
        <img class="pause" on:click={handlePause} alt="pause" src="build/assets/pause.png">
    </div>
</div>

