<script lang="ts">;
import { afterUpdate } from 'svelte';
import { playSound } from './stores/audio';
let style = '';
export let chip;
function getChipStyle(position) {
    let builtStyle = `top: ${position.y}px;left: ${position.x}px;z-index:${position.z || 0}`;
    if (position.rotation !== 0) {
        builtStyle += `;transform: rotate(${position.rotation}deg)`;
    }
    return builtStyle;
}
let lastPosition = {
    x: -100,
    y: -100,
    rotation: 0,
};
$: {
    style = getChipStyle(chip.position);
    if (chip.position.x !== lastPosition.x || chip.position.y !== lastPosition.y) {
        const sound = chip.position.y < 0 ? 'chip-high' : chip.position.y > 1080 ? 'chip' : 'chip-low';
        playSound(sound, Math.random() * 100);
    }
    lastPosition = chip.position;
}
</script>

<div class="chip" style="{style}">
</div>

<style>
.chip {
       position: absolute;
       width: 40px;
       height: 40px;
       background-image: url('assets/chip.png');
       background-size: cover;
       transform-origin: top left;
       transition: all .25s;
   }
</style>