<script lang="ts">import World from './World.svelte';
import { afterUpdate } from 'svelte';
import { ended } from './stores/user';
import { get } from 'svelte/store';
import { tableState } from './stores/table';
import { getPlayerBankChips } from './stores/position';
let isStarted = false;
let width;
let height;
let scale = 1;
let left = '0';
let top = '0';
function start() {
    isStarted = true;
}
function reset() {
    window.location.reload();
}
$: {
    scale = Math.min(width / 1920, height / 1080);
    const scaledWidth = 1920 * scale;
    const scaledHeight = 1080 * scale;
    left = `${Math.floor((width - scaledWidth) / 2)}px`;
    top = `${Math.floor((height - scaledHeight) / 2)}px`;
}
</script>

<div class="game-window"
	 style="--scale: {scale};--left: {left};--top: {top}">
	{#if isStarted && !$ended}
		<World/>
	{:else if !isStarted}
		<div class="title" on:click={start}>
			<p>Hey, it looks like we're gonna need each other's help if we wanna get through today, so listen up.</p>
			<p>There's five people in this casino that have it out for us, and they'll all make their way to this blackjack table some time soon.</p>
			<p>If you can figure out who they are, place a bet with a number of chips that matches their space at the table and then sit out the next hand. I can handle it from there.</p>
			<p>I'll feed you the info you need to find them as I get it. Be careful though, if you're too stingy or run out of money before finding them they'll be on you before I can do much to help.</p>
			<p>Good luck!</p>
			<p>&nbsp;</p>
			<p>Click anywhere to start...</p>
		</div>
	{:else if $ended === 'WIN'}
		<div class="title">
			<p>You win!!!</p>
			<p>Score {getPlayerBankChips($tableState, 2).length}</p>
			<p>&nbsp;</p>
			<p>&nbsp;</p>
			<p>Sorry, I'll put more effort into this screen later...</p>
		</div>
	{:else if $ended === 'LOSE'}
		<div class="title" on:click={reset}>
			<p>Ouch, looks like you pointed out the wrong person and got jumped.</p>
			<p>&nbsp;</p>
			<p>Click anywhere to try again...</p>
		</div>
	{:else if $ended === 'LOSE_MONEY'}
		<div class="title" on:click={reset}>
			<p>Ouch, looks like you ran out of money and got jumped.</p>
			<p>&nbsp;</p>
			<p>Click anywhere to try again...</p>
		</div>
	{/if}
</div>

<svelte:window bind:innerWidth={width}
               bind:innerHeight={height}/>

<style>
:global(*) {
	backface-visibility: hidden;
	-webkit-backface-visibility: hidden;
	-moz-backface-visibility: hidden;
	-ms-backface-visibility: hidden;
}

@font-face {
	font-family: dosis;
	src: url(assets/Dosis-Bold.ttf);
}

@font-face {
	font-family: written;
	src: url(assets/ShadowsIntoLight-Regular.ttf);
}

.title {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.title p {
	width: 60%;
	font-family: dosis;
	font-size: 42px;
	text-align: center;
	color: #eee;
	margin: 10px;
	padding: 0;
	cursor: default;
	user-select: none;
}

.game-window {
	--refreshvar: 1221;
	position: relative;
	width: 1920px;
	height: 1080px;
	left: var(--left);
	top: var(--top);
	overflow: hidden;
	transform: scale(var(--scale));
	transform-origin: left top;
}
</style>