<script lang="ts">import { createEventDispatcher } from 'svelte';
;
import { bets, choices } from './stores/user';
const dispatch = createEventDispatcher();
function choose(choice) {
    dispatch('choose', {
        value: choice,
    });
}
function bet(amount) {
    dispatch('bet', {
        value: amount,
    });
}
</script>

{#if $choices.length > 0 || $bets.length > 0}
    <div class="decide">
        <div class="decide-bkg"></div>
        {#if $choices.length > 0}
            <div class="decide-contents">
                {#each $choices as choice}
                    <button on:click="{e => choose(choice)}">{choice}</button>
                {/each}
            </div>
        {/if}
        {#if $bets.length > 0}
            <div class="bets-contents">
                <button on:click="{e => bet(0)}">Sit Out</button>
                {#each $bets as amount}
                    <button on:click="{e => bet(amount)}">{amount}</button>
                {/each}
            </div>
        {/if}
    </div>
{/if}

