<script lang="ts">;
export let count;
export let position;
$: style = position ? `left: ${position.x}px;top: ${position.y}px;transform: rotate(${position.rotation}deg)` : '';
</script>

{#if count > 0}
    <div class="count" style={style}>{count}</div>
{/if}

<style>
.count {
    position: absolute;
    z-index: 99999998;
    width: 40px;
    height: 36px;
    font-family: dosis;
    font-size: 24px;
    background-color: black;
    border: 0 px solid black;
    color: white;
    border-radius: 10px;
